ul.drinks-list {
  display: block;

  li.drinks-list {
    border: 1px solid black;
    cursor: pointer;
    display: block;
    height: 30vh;
    position: relative;
  }

  li.drinks-list-selected {
    height: 50vh !important;
  }

  li.drinks-list:nth-child(odd) {
    background-color: #111;
    color: #eee;
  }

  li.drinks-list:nth-child(even) {
    background-color: #eee;
    color: #111;
  }
}
