div.drink {
  display: block;
  height: 100%;
  margin: 0 auto;
  overflow: auto;
  position: relative;
  width: 79%;

  div.drink-img {
    // background-color: maroon;

    display: block;
    float: left;
    height: 100%;
    position: relative;
    width: 39%;

    img.drink {
      height: 61%;
    }
  }

  //

  section.drink {
    // background-color: gray;

    display: block;
    float: right;
    height: 100%;
    position: relative;
    width: 61%;

    h3.drink {
      clear: both;
      height: 21%;
      position: relative;
    }

    div.drink-ingredients {
      // background-color: greenyellow;

      display: block;
      float: left;
      height: 79%;
      position: relative;
      width: 61%;

      ul.drink {
        // background-color: lightcoral;
        left: 8%;
        margin-right: -8%;
        position: absolute;
        top: 50%;
        transform: translate(8%, -50%);

        max-width: 100%;
      }

      li.drink {
        margin: 0.61em 0;
        max-width: 100%;
      }
    }

    aside.drink {
      // background-color: khaki;

      display: block;
      float: right;
      height: 79%;
      position: relative;
      width: 39%;
    }
  }
}
