#root {
  font-size: larger;
}

#app {
  display: grid;
  grid-template-columns: 6fr 4fr;
  grid-template-rows: 1fr;
  grid-column-gap: 0px;
  grid-row-gap: 0px;

  height: 100%;
  letter-spacing: 0.3em;
}

#app-view-wrapper {
  background-color: olivedrab;
}

#app-dashboard-wrapper {
  background-color: silver;
}

h3 {
  font-size: 139%;
  font-weight: normal;
}

button {
  background-color: #f6f6f6;
  border-radius: 1.61em;
  // color: #eee;
  cursor: pointer;
  padding: 0.69em;
}

.cursor {
  cursor: pointer;
}

.center {
  left: 50%;
  margin-right: -50%;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
}

.center-up {
  left: 50%;
  margin-right: -50%;
  position: absolute;
  top: 21%;
  transform: translate(-50%, -21%);
}

.gr-up {
  left: 50%;
  margin-right: -50%;
  position: absolute;
  top: 39%;
  transform: translate(-50%, -61%);
}

.gr-down {
  left: 50%;
  margin-right: -50%;
  position: absolute;
  top: 61%;
  transform: translate(-50%, -39%);
}

.gr-down-left {
  left: 39%;
  margin-right: -61%;
  position: absolute;
  top: 61%;
  transform: translate(-61%, -39%);
}

.gr-left {
  left: 39%;
  margin-right: -61%;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -39%);
}

