div.category {
    height: 100%;
    overflow: auto;

    aside.category {
        background-image: linear-gradient(to right, rgba(1, 0, 0, 0), rgba(0, 0, 0, 1));
        color: #eee;
        height: 100%;
        max-height: 100%;
        position: fixed;
        right: 0;
        top: 0;
        width: 13%;

        h2.category {
            height: 61%;
            position: relative;
            text-orientation: upright;
            width: 100%;
            writing-mode: vertical-rl;

            // span.category {
            // }
        }

        div.category {
            height: 39%;
            position: relative;

            button.category {
                bottom: 3em;
                padding: 1em;
                position: absolute;
                right: 3em;
            }
        }
    }
}
