div.drinks-list-item {
  display: block;
  height: 100%;

  h3.drinks-list-item {
    height: 39%;
    position: relative;
  }

  section.drinks-list-item {
    position: relative;
    height: 61%;
  }

  em.drinks-list-item {
    display: inline-block;
    margin: 0 1.6em;
    max-width: 0.1em;
  }
}

li.drinks-list:nth-child(odd) {
  em.drinks-list-item {
    background-color: #eee;
  }
}

li.drinks-list:nth-child(even) {
  em.drinks-list-item {
    background-color: #111;
  }
}
