ul.categoriess {
    height: 100%;
    max-height: 100%;

    display: flex;
    flex-flow: row wrap;
    // This defines the alignment along the main axis.
    justify-content: space-around;
    // This defines the default behavior for how flex items are laid out along the cross axis on the current line.
    align-items: stretch;
    // This aligns a flex container's lines within when there is extra space in the cross-axis
    align-content: space-around;

    text-align: center;

    li.categories:nth-child(odd) {
        background-color: #111;
        color: #eee;
    }

    li.categories:nth-child(even) {
        background-color: #eee;
        color: #111;
    }

    li.categories {
        // flex-shrink: 3;
        flex-grow: 2;
        flex-basis: 30%;
        max-width: 50%;
        min-height: 50%;
        height: 50%;

        //
        cursor: pointer;
        overflow: hidden;

        max-height: 50%;
        display: block;

        position: relative;
    }

    div.categories {
        position: relative;
        display: block;
        height: 61%;
        overflow: hidden;
    }

    img.categories {
        max-height: 61%;
        width: auto;
    }

    h2.categories {
        position: relative;
        height: 39%;
        font-weight: normal;
    }
}
